<script>
import Search from "../../../src/views/HelpAndGuide/Search.vue";
import DataService from '../../services/DataService';

import { storeToRefs } from "pinia";
import { useloggedUserInfo } from "@/stores/UserInfo";
const { GetUserInfo } = storeToRefs(useloggedUserInfo());

import VueBootstrapTypeahead from 'vue-bootstrap-typeahead'
import VueEasyLightbox from 'vue-easy-lightbox'

export default {
    name: 'HelpArticles',

    components: {
        Search,
        VueBootstrapTypeahead,
        VueEasyLightbox,

    },
    data() {
        return {
            HelpArticleList: [],
            ArticleNameList: [],
            Article: '',
            ArticlesContent: '',
            IsPopularSearchShow: true,
            IsDataNotFoundShow: false,
            IsAccordionShow: false,
            isLoading: false,
            Path: Boolean,
            NewValue: Boolean,
            UserType: "",
            PopularSearchesList: [],
            PageLoadMsg: false,
            visibleRef: false,
            imgpath: '',

        }
    },
    created: async function () {

        this.UserType = GetUserInfo.value.UserType;
        await this.ArticleList();
        await this.GetPopularSearches();

    },
    mounted() {

        window.handleClick = this.handleClick;
        window.HandleHover = this.HandleHover;

    },
    methods: {

        HandleHover: function (e) {


            if ($(e).parent().hasClass("after-magnifier-for-image") == false) {
                $(e).after("<div style='display: inline-block;width: " + $(e).width() + "px;margin-right:10px;' class='after-magnifier-for-image'><span onclick= \"handleClick('" + $(e).attr("src") + "')\" style='width: " + $(e).width() + "px;height: " + $(e).height() + "px;'><i class='fa fa-search'></i></span><img style='max-width: 100% !important;padding-right:0px !important;' src='" + $(e).attr("src") + "' /></div>")
                $(e).remove();
            }
        },
        handleClick(src) {


            this.imgpath = src;
            this.visibleRef = true;

        },

        onHide() {

            this.visibleRef = false;
        },
        ArticleList: async function () {

            var vm = this;

            //vm.PageLoadMsg = true;
            vm.isLoading = true;

            var params = `search=&userType=${GetUserInfo.value.UserType}&articleType=HelpArticle&moduleId=`

            await DataService.GetArticleList(params).then(function (response) {

                let HelpArticle = JSON.parse(response.data[0].Value).Data;

                if (HelpArticle != null && HelpArticle.length > 0) {

                    HelpArticle.forEach((value, index) => {

                        vm.ArticleNameList.push(value.ArticleName)

                    })

                    //vm.PageLoadMsg = false;
                    vm.isLoading = false;

                }

                else {
                    //vm.PageLoadMsg = false;
                    vm.isLoading = false;
                }

            });
        },
        SearchData: async function () {

            var vm = this;
            if (vm.Article != "") {
                vm.isLoading = true;
                var params = `search=${vm.Article}&userType=${GetUserInfo.value.UserType}&articleType=HelpArticle&moduleId=`;
                await DataService.GetArticleList(params).then(function (response) {

                    let Articles = JSON.parse(response.data[0].Value).Data;

                    if (Articles != null && Articles.length > 0) {

                        vm.HelpArticleList = Articles;

                        vm.IsPopularSearchShow = false;
                        vm.IsAccordionShow = true;
                        vm.isLoading = false;
                        vm.IsDataNotFoundShow = false;
                    }
                    else {

                        vm.IsPopularSearchShow = false;
                        vm.IsDataNotFoundShow = true;
                        vm.IsAccordionShow = false;
                        vm.isLoading = false;
                    }

                });
            }
            else {

                vm.ShowAlert(vm.$t("FillFealds"), "warning", true, vm.$t("Alert"));
            }

        },
        ClearData: function (e) {
            this.Article = '';
            const typeaheadInput = this.$refs.typeahead.$el.querySelector('input');
            if (typeaheadInput) {
                typeaheadInput.value = '';
                typeaheadInput.dispatchEvent(new Event('input'));
            }
            this.IsPopularSearchShow = true;
            this.IsDataNotFoundShow = false;
            this.IsAccordionShow = false;

        },
        GetArticleContent: async function (ArticlesId) {


            var vm = this;
            var params = `ArticleId=${ArticlesId}&articleType=HelpArticle`;
            await DataService.GetArticleDetailById(params).then(function (response) {

                if (response.data != null) {

                    vm.ArticlesContent = response.data.article_content;

                    vm.ArticlesContent = vm.ArticlesContent.replaceAll('<img ', '<img  onmouseenter="HandleHover(this)"');




                    const currentRoute = vm.$route.fullPath;
                    const targetRoute = `/Help/HelpArticles/${ArticlesId}`;

                    if (currentRoute !== targetRoute) {
                        vm.$router.push({ name: "HelpArticles", params: { ArticleId: ArticlesId } });
                    }


                }


            });
        },
        routeArticleById: function (item) {

            var vm = this;
            var ArticleId = item.ArticleId;
            var ModuleId = item.ModuleId;
            vm.$router.push({ name: "HelpArticlesPerModule", params: { ModuleId: ModuleId, Id: ArticleId } });
        },

        GetPopularSearches: async function () {

            var vm = this;

            var params = `userType=${GetUserInfo.value.UserType}`

            await DataService.GetPopularSearches(params).then(function (response) {

                if (response.data != null && response.data != undefined) {
                    if (response.data.Data.length > 0 && response.data.Data != null) {
                        response.data.Data.forEach((value, index) => {

                            let myobj = {
                                ArticleName: value.ArticleName,
                                ArticleId: value.ArticleId,
                                ModuleId: value.ModuleId,
                                Icon: '<em class="fa ' + value.Icon + '" aria-hidden="true"></em>',
                            }
                            vm.PopularSearchesList.push(myobj);
                        })
                    }
                }
            });
        },
    },
};
</script>

<style scoped>
.after-magnifier-for-image {
    position: relative;
}

.after-magnifier-for-image i {
    display: none;
}

.after-magnifier-for-image:hover i {
    display: initial;
    color: #b7b7b7;
    text-align: center;
    font-size: 30px;
    color: #fff;
}

.after-magnifier-for-image:hover span {
    position: absolute;
    width: 100%;
    height: 100%;
    display: grid;
    justify-items: center;
    align-items: center;
    z-index: 99;
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.342);
}
</style>
